:root {
  font-family: 'helvetica neue', helvetica, arial, sans-serif;
  --background: black;
  --text-color: white;
}

[data-theme = "light"] {
  --background: #fafaf8;
  --text-color: black;
}

html {
  height: 100%;
  display: flex;
  flex-direction: column;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: var(--background);
}

body > * {
      flex-shrink: 0;
}

.app {
  background-color: var(--background);
  min-height: 100%;
  flex-grow: 1;
}

#main-content {
  background-color: var(--background);
  color: var(--text-color);
  min-height: 50%;
}



thead th, tfoot th {
    font-family: 'Rock Salt', cursive;
}

#theme-selector {
  color: var(--text-color);
  font-weight: lighter;
}

#cal-table {

    width: 99%;
    border-collapse: collapse;
    font-size: small;
    table-layout: fixed;
}

th, td {
    vertical-align: top;
    padding-bottom: 10px;
    text-align: center;
}

#header-bar {
    width: 99%;
    border-collapse: collapse;
    margin: auto;
    vertical-align: bottom;
    margin-bottom: -25px;
}

#header-h1 {
  vertical-align: middle;
  text-shadow: 4px 4px 10px #D5E5F2;
  color: var(--text-color);
}

tbody td {
  text-align: left;
  background-color: var(--background);
}

tfoot th {
  text-align: right;
}

/*tbody tr:nth-child(odd) {*/
/*  background-color: var(--background);*/
/*}*/

/*tbody tr:nth-child(even) {*/
/*  background-color: var(--background);*/
/*}*/

.homeworkButton {
    border: double;
    border-radius: 8px;
    background-color: purple;
    color: white;
    font-size: medium !important;
}

.container {
  color: var(--text-color);
    border: solid 8px;
    border-radius: 15px;
    border-color: #32628C;
    /*padding-bottom: 10px;*/
    margin: auto;
    min-height: 100%;
}

#cal-link, #docs-link, #contact-link {
  letter-spacing: 3px;
  width: 97%;
  /*background-color: var(--background);*/
  color: var(--text-color);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  border-top-style: solid;
  border-bottom-style: none;
  border-left-style: solid;
  border-right-style: solid;
  border-top-color: #D5E5F2;
  border-right-color: #D5E5F2;
  border-left-color: #D5E5F2;
  border-width: 3px;
  padding-top: 8px;
  padding-bottom: 8px;
  text-shadow: 2px 2px 4px #999999;

}

table {
  width: 100%;
}

#header-background {
    position: absolute;
    top: 0;
    left: 0;
    object-fit: cover;
}

.video-wrapper {
    position: relative;
    overflow: hidden;
    width: 100%;
    background-color: var(--background)
}

.header-title-theme {
  position: relative;
}

@media (max-width: 750px) {
    #header-background { display: none; }
    .video-wrapper {
      background-size: cover;
    }
}

.footer-table {
  background-color: var(--background);
}

#light-theme, #dark-theme, #cal-link, #docs-link, #contact-link {
  cursor: pointer;
}